var defectListSetCtrl = {
	init:function(){
		this.event();
        // this.formQuery = fromQuery;
        // this.initFormData();
		app.formElementsRender($(".search-panel",this.page));
        this.retrunBtn();
        this.submitBtn();
	},
	event: function () {
		var top = ((window.innerHeight - 50 -70 )/2 + 50) + "px";
		$(".toggle-right-btn",this.page).css("top",top).on("click",function(e){
			myApp.closePanel('right');
		})
	},
	retrunBtn:function(){
		var _this = this;
		$(".btn-return",_this.page).on('click',function(){
			myApp.closePanel('right');
		});
	},
    submitBtn:function(){
        var _this = this;
        $(".btn-submit",_this.page).on('click',function(){
            // var formData = myApp.formToJSON('#defectListSetForm');
            // defectListView.changeRowData(_this.formQuery.rowIndex, formData);
            myApp.closePanel('right');
        });
    },
    // initFormData: function () {
    //     myApp.formFromJSON('#defectListSetForm', this.formQuery.rowData);
    // }

}

module.exports = defectListSetCtrl
